import React from 'react';
import { goToNextFocusThunk } from 'store/thunks/addressFullPage';
import styled from 'styled-components';
import { Input } from 'components/Input/Input';
import { useDispatch, useSelector } from 'hooks';
import { addressFullApartmentInputChange } from 'store/actions';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullApartment = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressFullPage }) => addressFullPage.apartmentInputValue);
    const handleInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullApartmentInputChange(e.target.value));
        },
        [dispatch],
    );
    const handleInputSubmit = React.useCallback(() => {
        dispatch(goToNextFocusThunk);
    }, [dispatch]);

    return (
        <StyledInput
            title="Квартира"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleInputSubmit}
        />
    );
};
