import { createAction, createEmptyAction } from 'tools/action-creator';

import {
    ShowItemsCommandPayload,
    AddItemCommandPayload,
    RemoveItemCommandPayload,
    RemoveAllCommandPayload,
    GoToCatalogCommandPayload,
    GoToWaitCommandPayload,
    ValidationOkCommandPayload,
    GoBackCommandPayload,
    AddressInputPayload,
    AddressInputAddPayload,
    AddressInputClearPayload,
    AddressFullInputAddPayload,
    AddressFullInputClearPayload,
    AddressFullValidationOkPayload,
    AddressFullValidationErrorPayload,
    AskAgainForOrderStatusPayload,
    AskAgainForOrderStatusFinalPayload,
} from '../constants/native';

export const showItems = (payload: ShowItemsCommandPayload) => createAction('SHOW_ITEMS', payload);

export const addItem = (payload: AddItemCommandPayload) => createAction('ADD_ITEM', payload);

interface RemoveItemCommandPayloadExtended extends RemoveItemCommandPayload {
    shouldRemoveMention?: boolean;
}
export const removeItem = (payload: RemoveItemCommandPayloadExtended) => createAction('REMOVE_ITEM', payload);

export const removeAll = (payload: RemoveAllCommandPayload) => createEmptyAction('REMOVE_ALL');

export const goToCatalog = (payload: GoToCatalogCommandPayload) => createAction('GO_TO_CATALOG', payload);

export const goBack = (payload: GoBackCommandPayload) => createEmptyAction('GO_BACK');

export const goToWait = (payload: GoToWaitCommandPayload) => createEmptyAction('GO_TO_WAIT');

export const validationOk = (payload: ValidationOkCommandPayload) => createAction('VALIDATION_OK', payload);

export const addressInput = (payload: AddressInputPayload) => createAction('ADDRESS_INPUT', payload);

export const addressInputAdd = (payload: AddressInputAddPayload) => createAction('ADDRESS_INPUT_ADD', payload);

export const addressInputClear = (payload: AddressInputClearPayload) => createEmptyAction('ADDRESS_INPUT_CLEAR');

export const addressFullInputAdd = (payload: AddressFullInputAddPayload) =>
    createAction('ADDRESS_FULL_INPUT_ADD', payload);

export const addressFullInputClear = (payload: AddressFullInputClearPayload) =>
    createEmptyAction('ADDRESS_FULL_INPUT_CLEAR');

export const addressFullValidationOk = (payload: AddressFullValidationOkPayload) =>
    createEmptyAction('ADDRESS_FULL_VALIDATION_OK');

export const addressFullValidationError = (payload: AddressFullValidationErrorPayload) =>
    createEmptyAction('ADDRESS_FULL_VALIDATION_ERROR');

export const askAgainForOrderStatus = (payload: AskAgainForOrderStatusPayload) =>
    createEmptyAction('ASK_AGAIN_FOR_ORDER_STATUS');

export const askAgainForOrderStatusFinal = (payload: AskAgainForOrderStatusFinalPayload) =>
    createEmptyAction('ASK_AGAIN_FOR_ORDER_STATUS_FINAL');
