import packageJSON from '../../package.json';

const PREFIX = packageJSON.cssPrefix;

interface ConfitionsInterface<T> {
    T?: boolean;
}

export default (blockName: string, delimeter = '__', modDelimeter = '_'): BemRenderer => {
    function renderer(...elements: string[]): string {
        if (!elements.length) {
            return PREFIX + blockName;
        }

        return elements.map((element) => PREFIX + blockName + delimeter + element).join(' ');
    }

    renderer.elem = (element: string, mod: string, modVal?: string): string =>
        PREFIX + blockName + delimeter + element + modDelimeter + mod + (modVal ? modDelimeter + modVal : '');

    renderer.mod = (mod: string, modVal?: string): string =>
        PREFIX + blockName + modDelimeter + mod + (modVal ? modDelimeter + modVal : '');

    renderer.modify = (conditions: ConfitionsInterface<string> = {}, elem?: string): string => {
        const elemToModify = elem ? blockName + delimeter + elem : blockName;

        return [
            PREFIX,
            elemToModify,
            ' ',
            Object.entries(conditions)
                .map(([mod, predicate]) => Boolean(predicate) && PREFIX + elemToModify + modDelimeter + mod)
                .filter(Boolean)
                .join(' '),
        ].join('');
    };

    renderer.all = (...values: string[]): string => values.join(' ');

    return renderer;
};
