import React from 'react';
import styled from 'styled-components';
import { useDispatch, useSelector, useThrottledFn } from 'hooks';
import { ROUTES } from 'store/constants/router';
import { push } from 'connected-react-router';

import addressIcon from './address.svg';

const StyledAddress = styled.div`
    display: flex;
    align-items: center;

    text-decoration: none;
    margin-left: 8px;

    border-radius: 40px;
    padding: 20px 40px;

    height: 80px;
    box-sizing: border-box;

    transition: 0.2s ease-in-out;
    will-change: background-color;

    &:focus {
        background: #08a652;
    }
`;

const StyledAddressIcon = styled.div`
    width: 32px;
    height: 38px;

    background-image: url(${addressIcon});
    background-repeat: no-repeat;
    background-size: contain;
`;

const StyledAddressText = styled.div`
    margin-left: 20px;
    height: 40px;
    line-height: 40px;
    color: #fff;
    font-size: 32px;
    font-weight: 600;
`;

const THROTTLE_TIMEOUT_MS = 200;

export const HeaderAddress: React.FC = () => {
    const dispatch = useDispatch();

    const { text, isVisible } = useSelector(({ header }) => header.address);

    const handleClick = useThrottledFn(() => dispatch(push(ROUTES.ADDRESS_SIMPLE)), THROTTLE_TIMEOUT_MS, undefined, [
        dispatch,
    ]);

    if (!isVisible) {
        return null;
    }

    return (
        <StyledAddress tabIndex={0} onClick={handleClick}>
            <StyledAddressIcon />
            <StyledAddressText>{text}</StyledAddressText>
        </StyledAddress>
    );
};
