import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks';
import { SHOW_ITEMS_MOCK } from 'mocks';

import { CartListItem } from '../ListItem/CartListItem';

const preventScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollTop = 0;
};

const StyledCartList = styled.div`
    position: relative;
    height: 792px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const StyledContainer = styled.div`
    position: relative;
    width: 832px;
    max-height: 608px;
    overflow: hidden;
    --spatial-navigation-contain: contain;
`;

interface StyledWrapperProps {
    YShift: number;
}
const StyledWrapper = styled.div<StyledWrapperProps>`
    overflow-x: hidden;
    width: 832px;
    padding-right: 10px;
    will-change: transform;
    transition: transform 0.5s ease-in-out;

    transform: translateY(-${({ YShift }) => YShift}px);
`;

const StyledTotalPrice = styled.div`
    margin-top: 32px;
    margin-left: 16px;
    line-height: 56px;
    font-size: 48px;
    font-weight: 600;
    color: #fff;
`;

const StyledMinimalPrice = styled.div`
    margin: 8px 0 0 16px;
    font-weight: 500;
    font-size: 32px;
    line-height: 40px;
    color: #f6650a;
`;

interface StyledGradientProps {
    view: 'top' | 'bottom';
    hidden?: boolean;
}
const StyledGradient = styled.div<StyledGradientProps>`
    position: absolute;
    left: 0;
    right: 0;
    height: 160px;
    z-index: 1;
    background: linear-gradient(0, #001124 0%, rgba(0, 17, 36, 0) 89.09%);
    transition: opacity .2s ease-in-out;

    ${({ view }) =>
        view === 'top' &&
        css`
            top: -1px;

            transform: rotate(180deg);
        `}

    ${({ view }) =>
        view === 'bottom' &&
        css`
            bottom: -1px;
        `}

    opacity: ${({ hidden }) => (hidden ? '0' : '1')};
`;

export const CartList: React.FC = () => {
    const { order, totalPrice, toMinimumPrice, topGradientHidden, bottomGradientHidden, wrapperYShift } = useSelector(
        ({ cartPage }) => cartPage,
    );

    React.useEffect(() => {
        if (process.env.REACT_APP_ENVIRONMENT === 'development') {
            if (!order.length) {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line
                    smart_app_data: {
                        command: 'SHOW_ITEMS',
                        stock: SHOW_ITEMS_MOCK,
                    },
                });

                Object.keys(SHOW_ITEMS_MOCK).forEach((itemId) => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: {
                            command: 'ADD_ITEM',
                            item: itemId,
                            count: String(Math.max(1, Math.floor(Math.random() * 5))),
                        },
                    });
                });
            }
        }
    }, [order]);

    return (
        <StyledCartList>
            <StyledContainer onScroll={preventScroll}>
                <StyledGradient view="top" hidden={topGradientHidden} />
                <StyledWrapper YShift={wrapperYShift}>
                    {order.map((itemId, index) => (
                        <CartListItem key={itemId} index={index + 1} id={itemId} />
                    ))}
                </StyledWrapper>
                <StyledGradient view="bottom" hidden={bottomGradientHidden} />
            </StyledContainer>
            <StyledTotalPrice>Сумма заказа {totalPrice} ₽</StyledTotalPrice>
            {toMinimumPrice ? (
                <StyledMinimalPrice>До минимального заказа еще {toMinimumPrice} ₽</StyledMinimalPrice>
            ) : (
                ''
            )}
        </StyledCartList>
    );
};
