import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks';

import { CatalogListItem } from '../ListItem/CatalogListItem';

const preventScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollLeft = 0;
};

const StyledCatalogList = styled.div`
    margin-top: 72px;
    height: 640px;
    overflow: hidden;
`;

const StyledContainer = styled.div`
    position: relative;
    height: 660px;
    overflow: hidden;
    --spatial-navigation-contain: contain;
`;

interface StyledWrapperProps {
    XShift: number;
}
const StyledWrapper = styled.div<StyledWrapperProps>`
    display: grid;
    grid-template-columns: repeat(auto-fill, 392px);
    grid-auto-flow: row;
    grid-gap: 32px;

    will-change: transform;
    width: max-content;
    min-width: 100%;

    padding: 24px 0 44px;
    height: 660px;
    box-sizing: border-box;

    transition: transform 0.4s ease-in-out;
    transform: translateX(${({ XShift }) => -1 * XShift}px);
`;

export const CatalogList: React.FC = () => {
    const order = useSelector(({ catalogPage }) => catalogPage.order);
    const wrapperXShift = useSelector(({ catalogPage }) => catalogPage.wrapperXShift);

    return (
        <StyledCatalogList>
            <StyledContainer onScroll={preventScroll}>
                <StyledWrapper XShift={wrapperXShift}>
                    {order.map((itemId) => (
                        <CatalogListItem key={itemId} id={itemId} />
                    ))}
                </StyledWrapper>
            </StyledContainer>
        </StyledCatalogList>
    );
};
