export const formatPhoneNumber = (phoneNumber: string) => {
    return phoneNumber.split('').reduce((result, digit, index) => {
        result += digit;

        switch (index) {
            case 1: {
                result += ' ';
                break;
            }

            case 4: {
                result += ' ';
                break;
            }

            case 7: {
                result += ' ';
                break;
            }

            case 9: {
                result += ' ';
                break;
            }

            default:
                break;
        }

        return result;
    }, '');
};
