import React from 'react';
import styled from 'styled-components';

const contentMap = {
    'sold-out': 'Раскупили',
    'few-left': 'Осталось мало',
    sale: 'Скидка',
};

const backgroundMap = {
    'sold-out': '#080808',
    'few-left': '#F6650A',
    sale: '#08A652',
};

const StyledBadge = styled.div`
    padding: 8px 16px;
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    color: #fff;
    border-radius: 16px;
    background-color: ${(props: BadgeProps) => backgroundMap[props.type]};
`;

export interface BadgeProps extends React.HTMLAttributes<HTMLDivElement> {
    type: 'sold-out' | 'few-left' | 'sale';
}

export const Badge: React.FC<BadgeProps> = ({ type, children, ...props }) => (
    <StyledBadge type={type} {...props}>
        {contentMap[type]} {children}
    </StyledBadge>
);
