import { createAction, createEmptyAction } from 'tools';

interface AddressFullPhoneNumberInputChangePayload {
    inputType: InputEvent['inputType'];
    data: InputEvent['data'];
}
export const addressFullPhoneNumberInputChange = (payload: AddressFullPhoneNumberInputChangePayload) =>
    createAction('addressFull/PHONE_NUMBER_INPUT_CHANGE', payload);

export const addressFullApartmentInputChange = (payload: string) =>
    createAction('addressFull/APARTMENT_INPUT_CHANGE', payload);

export const addressFullEntranceInputChange = (payload: string) =>
    createAction('addressFull/ENTRANCE_INPUT_CHANGE', payload);

export const addressFullFloorInputChange = (payload: string) => createAction('addressFull/FLOOR_INPUT_CHANGE', payload);

export const addressFullValidationSuccess = () => createEmptyAction('addressFull/VALIDATION_SUCCESS');
