import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector, useDispatch, useThrottledFn } from 'hooks';
import { Button } from 'components/Button/Button';
import { addItem, removeItem, selectCartItem } from 'store/actions';
import plusIcon from 'assets/icons/plus.svg';
import minusIcon from 'assets/icons/minus.svg';

const StyledIndexBadge = styled.div`
    position: absolute;
    left: 8px;
    top: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 64px;
    width: 64px;
    font-size: 28px;
    font-weight: 600;
    border-radius: 50%;
    color: rgba(0, 0, 0, 0.55);
    background-color: #eaeaec;
`;

interface StyledThumbProps {
    imageUrl: string;
}
const StyledThumb = styled.div<StyledThumbProps>`
    height: 120px;
    width: 120px;
    border-radius: 16px;
    margin-right: 32px;
    background-image: url('${({ imageUrl }) => imageUrl}');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
`;

const StyledInfo = styled.div`
    width: 384px;
    margin-right: 48px;
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

interface StyledNameProps {
    oneLine?: boolean;
}
const StyledName = styled.div<StyledNameProps>`
    line-height: 40px;
    font-size: 32px;
    font-weight: 500;
    color: #fff;

    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;

    ${({ oneLine }) =>
        oneLine &&
        css`
            -webkit-line-clamp: 1;
        `}
`;

const StyledPrice = styled.div`
    margin-top: 4px;
    height: 36px;
    line-height: 36px;
    font-size: 28px;
    color: rgba(255, 255, 255, 0.47);
`;

const StyledSoldOut = styled.div`
    margin-top: 4px;
    font-weight: 500;
    font-size: 28px;
    line-height: 36px;
    color: #f6650a;
`;

interface StyledSelectorProps {
    focused?: boolean;
}
const StyledSelector = styled.div<StyledSelectorProps>`
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 8px;
    box-sizing: border-box;

    width: 216px;
    height: 80px;
    border-radius: 40px;

    will-change: background-color;

    transition: 0.2s ease-in-out;

    ${({ focused }) =>
        focused &&
        css`
            background: rgba(255, 255, 255, 0.05);
        `}
`;

const StyledSelectorValue = styled.div`
    font-size: 32px;
    line-height: 40px;
    font-weight: 600;
    color: #fff;
`;

const CartListItemRemoveButton = styled(Button)`
    background: rgba(255, 255, 255, 0.05);

    &:focus {
        background-color: #e12d39;
    }
`;

interface StyledSelectorButtonProps {
    view: 'less' | 'more' | 'remove';
}
const StyledSelectorButton = styled.button<StyledSelectorButtonProps>`
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 32px;
    height: 64px;
    width: 64px;
    border: none;
    will-change: background-color;
    transition: 0.2s ease-in-out;

    &::before {
        content: '';

        height: 24px;
        width: 24px;

        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &[disabled] {
        opacity: 0.4;
    }

    ${({ view }) =>
        view === 'less' &&
        css`
            &:focus {
                background: #08a652;
            }

            &::before {
                background-image: url('${minusIcon}');
            }
        `}

    ${({ view }) =>
        view === 'more' &&
        css`
            &:focus {
                background: #08a652;
            }

            &::before {
                background-image: url('${plusIcon}');
            }
        `}

    ${({ view }) =>
        view === 'remove' &&
        css`
            transform: rotate(45deg);

            &:focus {
                background-color: #e12d39;
            }

            &::before {
                background-image: url('${plusIcon}');
            }
        `}
`;

interface StyledCartListItemProps {
    disabled?: boolean;
}
const StyledCartListItem = styled.div<StyledCartListItemProps>`
    position: relative;
    display: flex;
    height: 152px;
    width: 832px;
    align-items: center;
    padding: 16px;
    box-sizing: border-box;

    ${({ disabled }) =>
        disabled &&
        css`
            ${StyledIndexBadge}
            ${StyledThumb}
            ${StyledName}
            ${StyledPrice} {
                    opacity: .24;
            }

            ${StyledPrice} {
                color: #fff;
        `}
`;

export interface CartListItemProps {
    id: string;
    index: number;
}

export interface CartListItemViewProps {
    imageUrl: string;
    name: string;
    price: number;
    count: number;
    disabled?: boolean;
    moreButtonDisabled?: boolean;
}

const THROTTLE_TIMEOUT_MS = 100;

export const CartListItem: React.FC<CartListItemProps> = ({ index, id }) => {
    const { imageUrl, name, price, count, disabled, moreButtonDisabled } = useSelector(
        ({ cartPage: { itemsMap } }) => itemsMap[id],
    );
    const dispatch = useDispatch();

    const handleLessClick = useThrottledFn(
        () => {
            dispatch(
                removeItem({
                    item: id,
                    count: '1',
                    shouldRemoveMention: !count,
                }),
            );
        },
        THROTTLE_TIMEOUT_MS,
        undefined,
        [dispatch, id, count],
    );

    const handleMoreClick = useThrottledFn(
        () => {
            dispatch(
                addItem({
                    item: id,
                    count: '1',
                }),
            );
        },
        THROTTLE_TIMEOUT_MS,
        undefined,
        [dispatch, id],
    );

    const handleRemoveClick = useThrottledFn(
        () => {
            dispatch(
                removeItem({
                    item: id,
                    count: String(count),
                    shouldRemoveMention: true,
                }),
            );
        },
        THROTTLE_TIMEOUT_MS,
        undefined,
        [dispatch, id, count],
    );

    const handleFocus = React.useCallback(() => dispatch(selectCartItem(id)), [dispatch, id]);

    return (
        <StyledCartListItem>
            <StyledIndexBadge>{index}</StyledIndexBadge>
            <StyledThumb imageUrl={imageUrl} />
            <StyledInfo>
                <StyledName>{name}</StyledName>
                <StyledPrice>{price} ₽</StyledPrice>
                {disabled ? <StyledSoldOut>Раскупили</StyledSoldOut> : ''}
            </StyledInfo>
            {disabled ? (
                <CartListItemRemoveButton onFocus={handleFocus} onClick={handleRemoveClick}>
                    Удалить
                </CartListItemRemoveButton>
            ) : (
                <StyledSelector>
                    <StyledSelectorButton
                        type="button"
                        aria-label="less"
                        view={count ? 'less' : 'remove'}
                        onFocus={handleFocus}
                        onClick={handleLessClick}
                    />
                    <StyledSelectorValue>{count}</StyledSelectorValue>
                    <StyledSelectorButton
                        type="button"
                        aria-label="more"
                        view="more"
                        disabled={moreButtonDisabled}
                        onFocus={handleFocus}
                        onClick={handleMoreClick}
                    />
                </StyledSelector>
            )}
        </StyledCartListItem>
    );
};
