import React from 'react';
import styled from 'styled-components';
import { replace } from 'connected-react-router';
import { useDispatch, useSelector } from 'hooks';
import { Header } from 'components/Header/Header';
import { Button } from 'components/Button/Button';
import { ROUTES } from 'store/constants/router';

import backgroundImage from '../../assets/images/map.png';

import { Badge } from './Badge/Badge';

const StyledNoArea = styled.div`
    height: 100%;
    position: relative;
    background: #001124;
`;

const StyledHeader = styled(Header)`
    position: relative;
    z-index: 10;
`;

const StyledContainer = styled.div`
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 64px 0;

    z-index: 10;
`;

const StyledTitle = styled.div`
    margin-top: 48px;
    font-weight: bold;
    font-size: 72px;
    line-height: 72px;
    color: #fff;
`;

const StyledSubtitle = styled.div`
    margin-top: 12px;
    font-weight: 500;
    font-size: 32px;
    line-height: 40px;
    color: #fff;
`;

const StyledButton = styled(Button)`
    margin-top: 40px;
`;

const StyledBackground = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url(${backgroundImage});
    background-blend-mode: luminosity;

    z-index: 1;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        background: linear-gradient(0, #001124 0, rgba(0, 17, 36, 0) 65%);
        transform: rotate(-180deg);
    }

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        background: linear-gradient(0, #001124 0, rgba(0, 17, 36, 0) 65%);
    }
`;

export const NoArea: React.FC = () => {
    const dispatch = useDispatch();
    const address = useSelector(({ userData }) => userData.address);
    const handleButtonClick = React.useCallback(() => {
        dispatch(replace(ROUTES.ADDRESS_SIMPLE));
    }, [dispatch]);

    return (
        <StyledNoArea>
            <StyledHeader />
            <StyledContainer>
                <Badge />
                <StyledTitle>{address}</StyledTitle>
                <StyledSubtitle>К сожалению, пока не доставляем по этому адресу</StyledSubtitle>
                <StyledButton shouldFocusOnMount onClick={handleButtonClick}>
                    Изменить
                </StyledButton>
            </StyledContainer>
            <StyledBackground />
        </StyledNoArea>
    );
};
