import React from 'react';
import styled from 'styled-components';
import { useFocusOnMount } from 'hooks';

const StyledButton = styled.button`
    padding: 20px 40px;
    border-radius: 40px;
    display: flex;
    border: none;
    color: #fff;
    font-weight: 600;
    font-size: 32px;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.1);
    transition: background-color 0.3s ease-in-out;

    &:focus {
        background-color: #08a652;
    }

    &[disabled] {
        opacity: 0.5;
    }
`;

export interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
    shouldFocusOnMount?: boolean;
    forwardRef?: React.RefObject<unknown>;
}

export const Button: React.FC<ButtonProps> = ({ className, shouldFocusOnMount, forwardRef, ...props }) => {
    const mountFocusRef = useFocusOnMount<HTMLButtonElement>();
    const ref = forwardRef || shouldFocusOnMount ? mountFocusRef : null;

    return <StyledButton className={className} ref={ref} type="button" {...props} />;
};
