import { DGSuggestItem } from 'types/2gis-maps';
import { createAction } from 'tools/action-creator';

export const addressChangeInputValue = (payload: string) => createAction('address/CHANGE_INPUT_VALUE', payload);

export const addressSetSuggests = (payload: DGSuggestItem[]) => createAction('address/SET_SUGGESTS', payload);

export const addressSelectSuggestItem = (payload: number) => createAction('address/SELECT_SUGGEST_ITEM', payload);

export const addressClickSuggestItem = (payload: string) => createAction('address/CLICK_SUGGEST_ITEM', payload);
