import React from 'react';
import { push } from 'connected-react-router';
import styled from 'styled-components';
import { goToCart } from 'store/actions';
import { AppState } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { useDispatch, useSelector, useThrottledFn } from 'hooks';

import cartIcon from './cart.svg';

const StyledHeaderCart = styled.div`
    display: flex;
    align-items: center;
    margin-left: 8px;
    border-radius: 40px;
    padding: 20px 40px;
    height: 80px;
    box-sizing: border-box;
    transition: 0.2s ease-in-out;
    will-change: background-color;

    &:focus {
        background: #08a652;
    }
`;

const StyledIcon = styled.div`
    position: relative;
    width: 40px;
    height: 36px;
    background-image: url('${cartIcon}');
    background-repeat: no-repeat;
    background-size: contain;
`;

const StyledBadge = styled.span`
    position: absolute;
    top: -14px;
    right: -12px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 32px;
    height: 32px;
    line-height: 28px;

    font-size: 20px;

    box-sizing: border-box;

    color: #fff;
    border-radius: 50%;
    background: #08a652;

    transition: 0.2s ease-in-out;
    will-change: background-color;

    ${StyledHeaderCart}:focus & {
        color: #08a652;

        background: #fff;
        border: 2px solid #08a652;
    }
`;

const StyledPrice = styled.div`
    height: 48px;
    margin-left: 16px;
    font-size: 32px;
    font-weight: 600;
    line-height: 50px;
    color: #fff;
`;

const THROTTLE_TIMEOUT_MS = 200;

export const HeaderCart: React.FC = () => {
    const dispatch = useDispatch();

    const { count, price, isVisible } = useSelector(({ header }: AppState) => header.cart);

    const handleClick = useThrottledFn(
        () => {
            dispatch(push(ROUTES.CART));
            dispatch(goToCart());
        },
        THROTTLE_TIMEOUT_MS,
        undefined,
        [dispatch],
    );

    if (!isVisible) {
        return null;
    }

    return (
        <StyledHeaderCart tabIndex={0} onClick={handleClick}>
            <StyledIcon>
                <StyledBadge>{count}</StyledBadge>
            </StyledIcon>
            <StyledPrice>{price}&nbsp;₽</StyledPrice>
        </StyledHeaderCart>
    );
};
