import React from 'react';
import { Skeleton } from 'components/Skeleton/Skeleton';
import withBem from 'hoc/withBem';

import './CatalogSkeleton.scss';

type CatalogSkeletonProps = BemProps;

const itemWidth = 392;
const itemHeight = 592;
const columnCount = 5;
const paddingBetween = 32;

export const Component: React.FC<CatalogSkeletonProps> = ({ bem }) => (
    <div className={bem()}>
        <Skeleton
            viewBox={`0 0 ${itemWidth * columnCount + paddingBetween * (columnCount - 1)} ${itemHeight}`}
            width="100%"
            height="100%"
            backgroundColor="#262828"
            foregroundColor="#414545"
            itemWidth={itemWidth}
            itemHeight={itemHeight}
            borderRadius={24}
            rowCount={2}
            columnCount={5}
            paddingBetweenRows={paddingBetween}
            paddingBetweenColumns={paddingBetween}
            speed={2}
        />
    </div>
);

export const CatalogSkeleton = withBem(Component, 'CatalogSkeleton');
