import { addressFullValidationOk } from 'store/actions';
import { AppThunkAction } from 'store/constants';
import { goBack, replace } from 'connected-react-router';
import { AddressFullValidationOkPayload } from 'store/constants/native';
import { ROUTES } from 'store/constants/router';

export const goToNextFocusThunk: AppThunkAction = (dispatch, getState) => {
    const state = getState();
    const {
        addressFullPage: { nextFocus },
    } = state;

    if (nextFocus === ROUTES.ADDRESS_FULL_ALL) {
        dispatch(goBack());
    } else {
        dispatch(replace(nextFocus));
    }
};

export const addressFullValidationOkThunk = (payload: AddressFullValidationOkPayload): AppThunkAction => (
    dispatch,
    getState,
) => {
    const state = getState();
    const {
        addressFullPage: { nextFocus },
    } = state;

    dispatch(addressFullValidationOk(payload));
    dispatch(replace(nextFocus));
};
