import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks';
import { Header } from 'components/Header/Header';
import mapImage from 'assets/images/map.png';

import { CartInfo } from './Info/CartInfo';
import { CartList } from './List/CartList';

const StyledBackground = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    background-image: url('${mapImage}');

    &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;

        background: linear-gradient(90.98deg, #001124 50.03%, rgba(0, 17, 36, 0) 99.56%);
    }
`;

const StyledContainer = styled.div`
    display: flex;
    position: relative;
    justify-content: space-between;
    padding: 0 112px;
`;

const StyledOverlap = styled.div`
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    background: rgba(0, 0, 0, 0.6);
`;

export const Cart: React.FC = () => {
    const { isOrderStatusWaiting } = useSelector(({ cartPage }) => cartPage);

    return (
        <>
            <StyledBackground />
            <Header />
            <StyledContainer>
                <CartList />
                <CartInfo />
            </StyledContainer>
            {isOrderStatusWaiting ? <StyledOverlap /> : ''}
        </>
    );
};
