import { splitByThousands } from 'tools';
import { AppState, AppReducer } from 'store/constants';
import { ROUTES } from 'store/constants/router';

const routeTitleMap: Record<string, string> = {
    [ROUTES.CATALOG]: 'Самокат',
    [ROUTES.CART]: 'Корзина',
    [ROUTES.STATUS]: 'Самокат',
    [ROUTES.NO_AREA]: 'Адрес доставки',
    [ROUTES.ADDRESS_FULL_ALL]: 'Адрес доставки и телефон',
};

const getTitle = ({
    router: {
        location: { pathname },
    },
}: AppState) => routeTitleMap[pathname] || '';

const getAddress = ({
    router: {
        location: { pathname },
    },
    userData: { address },
}: AppState) => ({
    text: address,
    isVisible: pathname === ROUTES.CATALOG,
});

const getCart = ({
    router: {
        location: { pathname },
    },
    cart: { totalCount, totalPrice },
}: AppState) => ({
    isVisible: totalCount > 0 && pathname === ROUTES.CATALOG,
    count: totalCount,
    price: splitByThousands(Math.ceil(totalPrice / 100)),
});

export const headerView: AppReducer = (state) => {
    const { header } = state;

    header.title = getTitle(state);

    header.address = getAddress(state);
    header.cart = getCart(state);
    header.isBackButtonVisible = Boolean(window.history.length);
};
