import React from 'react';
import { goToNextFocusThunk } from 'store/thunks/addressFullPage';
import styled from 'styled-components';
import { Input } from 'components/Input/Input';
import { useDispatch, useSelector } from 'hooks';
import { addressFullPhoneNumberInputChange } from 'store/actions';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullPhone = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressFullPage }) => addressFullPage.phoneNumberInputValue);
    const handleInputChange = React.useCallback(
        ({ nativeEvent }: React.ChangeEvent<HTMLInputElement>) => {
            // без этого тайпгварда TS не хочет показывать правильные поля
            if (nativeEvent instanceof InputEvent) {
                const { inputType, data } = nativeEvent;

                dispatch(addressFullPhoneNumberInputChange({ inputType, data }));
            }
        },
        [dispatch],
    );
    const handleInputSubmit = React.useCallback(() => {
        dispatch(goToNextFocusThunk);
    }, [dispatch]);

    return (
        <StyledInput
            title="Номер телефона"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleInputSubmit}
        />
    );
};
