import React from 'react';
import styled, { css } from 'styled-components';
import { addItem, selectCatalogItem } from 'store/actions';
import { useDispatch, useFocusOnMount, useSelector, useThrottledFn } from 'hooks';
import { Badge, BadgeProps } from 'components/Badge/Badge';
import plusIcon from 'assets/icons/plus.svg';

const StyledDescription = styled.div`
    position: relative;
    padding: 32px 32px 36px;
    box-sizing: border-box;
    height: 200px;
    background: rgba(255, 255, 255, 0.05);
`;

const StyledName = styled.div`
    height: 80px;
    line-height: 40px;
    font-size: 32px;
    font-weight: 500;
    color: #fff;

    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
`;

const StyledPriceContainer = styled.div`
    margin-top: 12px;
    display: flex;
`;

const StyledPrice = styled.div`
    height: 40px;
    line-height: 40px;
    font-size: 32px;
    font-weight: 600;
    color: #fff;
`;

const StyledCount = styled.div`
    height: 40px;
    line-height: 40px;
    margin-left: 8px;
    font-size: 32px;
    font-weight: 600;
    color: #08a652;
`;

interface StyledThumbProps {
    imageUrl: string;
}
const StyledThumb = styled.div<StyledThumbProps>`
    display: block;
    height: 392px;
    width: 392px;

    background-image: url('${(props) => props.imageUrl}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

interface StyledIndexBadgeProps {
    visible: boolean;
}

const StyledIndexBadge = styled.div<StyledIndexBadgeProps>`
    position: absolute;
    left: 16px;
    top: 16px;

    height: 64px;
    width: 64px;
    text-align: center;
    line-height: 64px;
    font-size: 28px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.55);

    background: #eaeaec;
    border-radius: 40px;

    transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;

    ${({ visible }) =>
        visible
            ? css`
                  transform: scale(1);
                  opacity: 1;
              `
            : css`
                  transform: scale(0);
                  opacity: 0;
              `}
`;

interface StyledButtonProps {
    disabled?: boolean;
}
const StyledButton = styled.div<StyledButtonProps>`
    position: absolute;
    top: -52px;
    right: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 72px;
    height: 72px;
    opacity: 0;
    border-radius: 50%;
    background: ${({ disabled }) => (disabled ? '#3b4b5e' : '#08a652')};
    transition: opacity 0.2s ease-in-out;

    &::before {
        content: '';

        display: block;

        height: 32px;
        width: 32px;

        opacity: ${({ disabled }) => (disabled ? '0.5' : '1')};

        transition: background 0.2s ease-in-out, opacity 0.2s ease-in-out;

        background-image: url('${plusIcon}');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }
`;

const CatalogListItemBadge = styled(Badge)`
    position: absolute;
    top: 16px;
    right: 16px;
    z-index: 1;
`;

interface StyledCatalogListItemProps {
    disabled?: boolean;
}
const StyledCatalogListItem = styled.div<StyledCatalogListItemProps>`
    position: relative;
    width: 392px;
    height: 592px;

    display: flex;
    flex-direction: column;
    grid-row: 1;

    overflow: hidden;
    border-radius: 24px;
    box-shadow: 0 16px 48px rgba(0, 0, 0, 0.1);

    transition: 0.4s ease-in-out;
    will-change: transform;
    will-change: background-color;

    &:focus {
        transform: scale(1.08);

        background-color: rgba(255, 255, 255, 0.1);

        ${({ disabled }) =>
            !disabled &&
            css`
                ${StyledButton} {
                    opacity: 1;
                }
            `}
    }

    ${({ disabled }) =>
        disabled &&
        css`
            ${StyledDescription},
            ${StyledThumb} {
                opacity: 0.5;
            }
        `}
`;

const THROTTLE_TIMEOUT_MS = 150;

export interface CatalogListItemProps {
    id: string;
}

export interface CatalogListItemViewProps {
    shouldFocusOnMount: boolean;
    imageUrl: string;
    count: number;
    name: string;
    index: number;
    price: number;
    badge?: BadgeProps;
    disabled?: boolean;
    buttonDisabled?: boolean;
}

export const CatalogListItem: React.FC<CatalogListItemProps> = ({ id }) => {
    const dispatch = useDispatch();
    const { shouldFocusOnMount, imageUrl, count, index, name, price, badge, disabled, buttonDisabled } = useSelector(
        ({ catalogPage }) => catalogPage.itemsMap[id],
    );

    const handleClick = useThrottledFn(
        () => {
            if (!disabled) {
                dispatch(addItem({ item: id, count: '1' }));
            }
        },
        THROTTLE_TIMEOUT_MS,
        undefined,
        [dispatch, id, disabled],
    );

    const handleFocus = React.useCallback(() => dispatch(selectCatalogItem(id)), [dispatch, id]);

    const onMountRef = useFocusOnMount<HTMLDivElement>();

    return (
        <StyledCatalogListItem
            ref={shouldFocusOnMount ? onMountRef : undefined}
            tabIndex={0}
            disabled={disabled}
            onFocus={handleFocus}
            onClick={handleClick}
        >
            {badge ? <CatalogListItemBadge {...badge} /> : ''}
            <StyledIndexBadge visible={index > 0}>{index}</StyledIndexBadge>
            <StyledThumb imageUrl={imageUrl} />
            <StyledDescription>
                <StyledButton disabled={buttonDisabled} />
                <StyledName>{name}</StyledName>
                <StyledPriceContainer>
                    <StyledPrice>{price} ₽</StyledPrice>
                    {count ? <StyledCount>×{count}</StyledCount> : ''}
                </StyledPriceContainer>
            </StyledDescription>
        </StyledCatalogListItem>
    );
};
