import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks';

import { HeaderAddress } from '../HeaderAddress/HeaderAddress';
import { HeaderCart } from '../HeaderCart/HeaderCart';

import backButtonIcon from './chevron-right.svg';
import logoIcon from './logo.svg';

const StyledHeader = styled.div`
    position: relative;

    display: flex;
    justify-content: space-between;
    align-items: center;

    box-sizing: border-box;

    --spatial-navigation-contain: contain;

    height: 160px;

    padding: 60px 88px 28px 128px;
`;

const StyledBackButton = styled.span`
    position: absolute;
    top: 78px;
    left: 56px;

    width: 20px;
    height: 36px;

    background-image: url(${backButtonIcon});
    background-repeat: no-repeat;
    background-size: contain;

    transition: transform 0.2s ease-in-out;
`;

const StyledSection = styled.div`
    display: flex;
    align-items: center;
`;

const StyledLogo = styled.div`
    width: 72px;
    height: 72px;
    border-radius: 8px;

    background-image: url(${logoIcon});
    background-repeat: no-repeat;
    background-size: contain;
`;

const StyledTitle = styled.div`
    display: inline-block;
    margin-left: 24px;
    height: 48px;
    line-height: 44px;
    font-size: 40px;
    font-weight: bold;
    color: #fff;
`;

export const Header: React.FC = () => {
    const { title, isBackButtonVisible } = useSelector(({ header }) => header);

    return (
        <StyledHeader>
            {isBackButtonVisible && <StyledBackButton />}
            <StyledSection>
                <StyledLogo />
                <StyledTitle>{title}</StyledTitle>
            </StyledSection>
            <StyledSection>
                <HeaderAddress />
                <HeaderCart />
            </StyledSection>
        </StyledHeader>
    );
};
